import QR from "../../static/js/wxqrcode.js"
export default {
	data() {
		return {
			img:'',
			val:''
		}
	},  
	methods: {
		creats:function(e){
			this.img = QR.createQrCodeImg("http://w.tcloudme.com/#/?e="+e, {
			     size: parseInt(100)//二维码大小  
			})
		}
	}
}
